@import url('https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,300;0,400;1,500&display=swap');

.image{
    flex-grow: 1;
    max-height: 85%;
    object-fit: contain;
}
  
.image_container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.image_information{
    padding: 5px;
    height: 15%;
    text-align: left;
    font-size: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.image_information p{
    margin: 0;
}

.text{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 10px;
    font-family: "Cormorant Garamond", serif;
    font-weight: 500;
    font-size: 1.3rem;
}

.image_name{
    font-size: 2.4rem;
    font-weight: 300;
    text-align: center;
    margin-bottom: 10px;
}


.shot_info_container{
    display: flex;
    flex-direction: row;
}

.text img{
    width: 32px;
}


.image_frame{
    background:rgb(240, 238, 232);
    padding: 15px;
    padding-top: 25px;
    margin-bottom: 5vh;
    margin: 10px;
    height: 75vh;
    width: 35vw;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    box-shadow: 6px 6px 12px 0px rgba(0,0,0,0.2);
    cursor: pointer;
    transition: 0.25s ease-in-out;
}

.image_frame:hover{
    box-shadow: 6px 6px 12px 0px rgba(0,0,0,0.3);
    width: calc(35vw + 5px);
    height: calc(75vh + 10px);
    transform: rotate(0.5deg);
    z-index: 0;
}

.filler{
    flex: 1;
}

.popup{
    z-index: 2;
    position: fixed;
    top: 140vh;
    padding: 15px;
    padding-top: 25px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    left: 0vw;
    width: 45vw;
    height: 90vh;
    background:rgb(240, 238, 232);
    box-shadow: 6px 6px 8px 0px rgba(0,0,0,0.3), -6px -6px 8px 0px rgba(0,0,0,0.3);
    transition: 0.66s cubic-bezier( 0, 0, 0, 0.9);
    transform: rotate(45deg);
    
}

.close_popup{
    position: absolute;
    top: 5px;
    right: 15px;
    font-size: 1.5rem;
    cursor: pointer;
    user-select: none;
}

.grey_out{
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0);
    transition: 0.25s ease-in-out;
    pointer-events: none;
    display: block;
}
